import {
	PARTNERINVITELIST,
	PARTNERINVITEDATA
} from '../../../utils/api.js'
export default {
	data() {
		return {
			listData: '',
			myInviteData: ''
		}
	},
	onLoad() {
		uni.showToast({
			title: '加载中...',
			icon: "none",
			mask: true
		})
		// 邀请明细
		this.inviteList()
		this.myInvite()
	},
	onPullDownRefresh() {
		this.inviteList()
		this.myInvite()
	},
	filters: {
		mobilechange: function(value) {
			var b = ''
			if (value) {
				var pat = /(\d{3})\d*(\d{4})/
				b = value.replace(pat, '$1****$2');
				return b
			} else {
				b = '未绑定'
				return b
			}

		},
		datechange: function(value) {
			var first = ''
			if (value) {
				first = value.split(" ")[0]
				if (first !== undefined) {
					return first
				}
			} else {
				return first
			}

		}
	},
	methods: {
		myInvite() {
			const params = {}
			this.$request.url_request(PARTNERINVITEDATA, params, 'GET', res => {
				this.myInviteData = JSON.parse(res.data).data
				uni.hideToast()
				uni.stopPullDownRefresh()
			}, err => {})
		},
		inviteList() {
			const params = {
				pageNum: 1,
				pageSize: 20
			}
			this.$request.url_request(PARTNERINVITELIST, params, 'GET', res => {
				this.listData = JSON.parse(res.data).data
				uni.hideToast()
				uni.stopPullDownRefresh()
				console.log(this.listData)
			}, err => {})
		}
	}
}
